.Entete{
    margin-top: 10px;
    background-color: white;
    height: 100px;
    border-radius: 10px;
    border: 1px solid rgb(178, 176, 176, 0.6);
}
.peronaImg, .peronaImg1{
    margin-top: 10px;
    background-color: #004275;
    border-radius: 50%;
    color: white !important;
}
.btnTopic{
    width: 60%;
    padding: 10px;
    border-radius: 100px;
    margin: 0 10px;
    margin-top: 20px;
    border: 2px solid #004275;
   
}
.btnShare{
    padding: 10px;
    width: 20%;
    border: none;
    outline: 0;
    margin-left: 5px;
    border-radius: 10px;
    background-color: #004275;
    color: yellow;
    transition: 0.6s ease-in;
    font-weight: bold;
    cursor: pointer;

}
.btnShare:hover{
    background-color: yellow;
    color: #004275;
}
.corpsdusujet{
    background-color: white;
    border-radius: 10px;
    min-height: 150px;
    margin: 5px 0 2px 0;

}
.partieLike{
    display: flex;
    justify-content: space-around;
    width: 100%;
}
.partieLike button{
    border: none;
    background: none;
}
.partieLike button:hover{
    background: rgba(128,128,128,0.1);
    /* padding: 10px; */
    
}

.parentpoint{
    position: relative;
}
.troispoint{
    cursor: pointer;
    position: absolute;
    right: 10px;
}
.like{
    flex: 1 1 50%;
    outline: 0;
    border: 0;
}
.comment{
    flex: 1 1 50%;
    outline: 0;
    border: 0;
}
@media (max-width: 895px) {

    .btnShare{
        padding: 9px;
        width: 90px;
        border: none;
        margin-right: 5px;
        height: 40px;
        
        outline: 0;
        border-radius: 8px;
        background-color: #004275;
        color: yellow;
        transition: 0.6s ease-in;
        font-weight: bold;
        cursor: pointer;
    
    }
    .btnTopic{
        width: 55%;
        padding: 8px;
        border-radius: 100px;
        margin: 0 5px;
        height: 50px;
        margin-top: 25px;
        border: 2px solid #004275;
       
    }
    
}


