.parentForm{
    background-color: rgb(237, 232, 232);
    margin: 0 5px;
    border-radius: 20px;
    box-shadow: 0  0 2px #004275;
}
.parentForm h3{
    color: #004275;
    text-align: center;
    padding: 10px 0;
}

.containerForm{
    display: flex;
    flex-direction: column;
    height: auto;
}
.containerForm input{
    padding: 10px;
    border: none;
    margin-top: 10px;
    outline: 0;
    width: 98%;
    margin-left: 5px;
    border-radius: 10px;
}
.bouton{
    padding: 10px;
    border: none;
    margin-top: 25px;
    outline: 0;
    width: 98%;
    margin-left: 5px;
    border-radius: 10px;
    /* margin-bottom: 20px; */
    background-color: #004275;
    color: yellow;
    transition: 0.6s ease-in;
    font-weight: bold;
    cursor: pointer;
}
.bouton:hover{
    background-color: yellow;
    color: #004275;
}

