.temoignages{
    height: 150px;
    width: 100%;
    background-color: #FFC900;
}
.temoignages h1{
    color: white;
    text-align: center;
    padding-top: 40px;
}
.temoignages p{
    color: white;
    text-align: center;
    font-size: 18px;
}
.temoin2, .temoin1, .temoin3{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
@media (max-width: 768px){
    .temoin2, .temoin1, .temoin3{
        display: flex;
        flex-direction: column;
    }
    img{
        width: 200px;
        display: block;
        margin: 0 auto;
    }
    .bloq{
        width: 360px;
        margin: 10px 10px;
    }
}