.parentFooter{
    color: white;
    display: flex;
    margin: 10px;
    /* width: 100%; */
    background: url('../../../Assets/Imgfooter.jpg');
    height: auto;
    background-repeat: no-repeat;
    background-size: cover;
}
.un{
flex: 1 1 32%;
border-right: 1px solid gray;
}
.deux{
    flex: 1 1 32%;
    margin-left: 20px;
    margin-top: 50px;
    text-align: center;
    border-right: 1px solid gray;
}
.trois{
    flex: 1 1 32%;
    margin-left: 20px;
    margin-top: 50px;
    text-align: center;
}
.rsn img{
    margin: 10px;
    display: inline-block;
    text-align: center;
}
@media (max-width: 768px){
    .parentFooter{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        border-radius: 10px;
    }
    .rsn img{
        /* display: inline-block; */
        margin-left: 20%;
    }
    .un, .deux{
        border-bottom: 1px solid gray;
    }
    .un:nth-child(1){
        margin-left: 36px;
    }
.deux{
    flex: 1 1 100%;
}    
}