
.eveParent{
    display: flex;
}
.eveEnfant1{
    flex: 1 1 30%;
    margin-top: 30px;
    margin-left: 20px;
}
.eveEnfant1 p{
    
    margin-right: 10px;
    font-size: 30px;
    letter-spacing: 1px;
}
.map{
    flex: 1 1 60%;
    /* margin-left: 150px; */
    margin-top: 10px;
}
.evenement{
    height: 150px;
    width: 100%;
    background-color: #FFC900;
}
.evenement h1{
    color: white;
    text-align: center;
    padding-top: 40px;
}
.evenement p{
    color: white;
    text-align: center;
    font-size: 18px;
}

  @media (max-width: 768px){
    .eveParent{
        display: flex;
        flex-direction: column;
        /* width: 100%; */
    }
    .eveEnfant1 button{
        display: block;
        margin: 0 auto;
    }
   
    
    .map iframe{
        border-radius: 10px;
        margin: 10px;
        width: 370px;
    }
  }
  