.article{
    height: 150px;
    width: 100%;
    background-color: #FFC900;
}
.article h1{
    color: white;
    text-align: center;
    padding-top: 40px;
}
.parentarticle1,.parentarticle2 {
    display: flex;
}
.parentarticle1 p{
    margin-top: 30px;
    line-height: 30px;
    letter-spacing: 2px;
    flex: 1 1 50%;
    margin-left: 20px;
}
.parentarticle2 p{
    margin-top: 30px;
    line-height: 30px;
    letter-spacing: 2px;
    flex: 1 1 50%;
    margin-left: 17px;
}

.parentarticle1 img{
    margin-right: 17px;
    flex: 1 1 50%;
}
.parentarticle2 img{
    margin-left: 20px;
    flex: 1 1 50%;
}
.article1 h2, .article2 h2{
    color: #004275;
    font-weight: bold;
    text-align: center;
    margin: 20px 0;
}
@media (max-width: 768px){
    .parentarticle1, .parentarticle2{
        display: block;
    }
    .parentarticle1 img{
        width: 370px;
        margin-left:15px;
        margin-right:10px;
        border-radius: 10px;
        
    }
    .parentarticle2 img{
        width: 365px;
        border-radius: 10px;
    }
}