.containerHome{
    width: 100% !important;
    display: flex;
    /* flex-direction: row; */
    flex-wrap: nowrap;
    height: 100%;
}

.partie1{
    background-color: rgb(222, 219, 219);
    flex: 1 1 25%;
    margin: 10px;
    height: 20% !important;
    width: 100px;
    border-radius: 10px;
    border: 1px solid rgb(178, 176, 176, 0.6);
}
.partie2{
    background-color: rgb(222, 219, 219);
    flex: 1 1 50%;
    margin: 10px 10px 10px 0;
    min-height: 400px;
    /* width: 45% !important; */
    
}
.partie3{
    background-color: rgb(222, 219, 219);
    flex: 1 1 25%;
    margin : 10px 10px 10px 0px;
    height: auto;
    border-radius: 10px;
    border: 1px solid rgb(178, 176, 176, 0.6);
}
.imagePersona{
    display: block;
    margin: 0 auto;
}

@media (max-width: 600px) {
    .containerHome{
        display: flex;
        flex-direction: column;
    }
    .partie1{
        display: none;
    }
    .partie2{
        margin: 10px 10px;
        border-radius: 10px;
    }
}
