.parentForum{
    display: flex;
    margin: 0;
    padding: 0;
    /* width: 90%; */
}
.h1{
    height: 150px;
    width: 100%;
    background-color: #FFC900;
}
.h1 h1{
    color: white;
    text-align: center;
    padding-top: 40px;
}
.h1 p{
    color: white;
    text-align: center;
    font-size: 18px;
}
.left{
    margin: 0 !important;
    flex: 1 1 55%; 
    background-image: url(../../../Assets/partage.jpg);
    background-color: rgb(207, 100, 100);
    height: 400px;
    background-position: center;
    background-size: cover;
}
.left h1{
    color: white;
    margin: 20% 20px;
}
.right{
    flex: 1 1 45%;
}

