@media  (max-width: 999px) and (min-width:768px){
    .parentForum{
        display: flex;
    }
        .left, .right{
            flex: 1 1 50%;
        }
  
}
@media (max-width: 767px){
    .parentForum{
        display: flex;
        flex-direction: column;
    }
    .left{
        margin-left: 10px !important;
        margin-right: 10px !important;
        border-radius: 20px;
        
    }
    .right{
        margin: 10px 10px !important;
    }
   
}